  opacity: 0; }

.visuallyhidden {
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  clip: rect(0 0 0 0); }

.no-scroll {
  position: fixed;
  overflow: hidden; }

.clearfix:after,
.cf:after {
  content: '';
  clear: both;
  display: table; }

